
<div class="field">
  <div
    v-if="samples >= 100"
    class="heading"
  >
    <p class="instructions">
      Average of <strong>{{ formatNumber(samples) }}</strong> data sample<span
        v-if="samples !== 1"
      >s</span>.
    </p>
  </div>
  <p
    v-if="samples < 100"
    class="warning"
  >
    Average of only <strong>{{ formatNumber(samples) }}</strong> data
    sample<span v-if="samples !== 1">s</span>.
  </p>
</div>
